/* 404 page css */
.not_found-err {
	display: flex;
	justify-content: center;
	font-size: 30px;
	position: relative;
	min-height: 75vh;
	align-items: center;
}

/*  About page css */

.about-page-css {
	background-color: rgb(228, 228, 228);
	display: flex;
	flex-direction: column;
	position: relative;
	min-height: 75vh;
	align-items: center;
	color: teal;
}
.about-page-css h1 {
	background-size: 1px;
	background-image: url(https://media.giphy.com/media/xUPGcfG7LQs4a7rIsM/giphy-downsized.gif);
	margin-top: 75px;
	font-size: 25px;
	color: rebeccapurple;
}

.about-page-css p {
	/* Adds color and lots of contrast */
	filter: contrast(150%);
	-webkit-filter: contrast(150%);
	-moz-filter: contrast(150%);
	-o-filter: contrast(150%);
	-ms-filter: contrast(150%);
	cursor: pointer;
	margin: 16px;
	box-shadow: 3px 3px 10px 1px #000000;
	font-size: 23px;
	color: rgb(5, 97, 97);
	background: #00c2ff;
	background: linear-gradient(241deg, #00c2ff 0%, #5be858 80%);
	background: -webkit-linear-gradient(241deg, #00c2ff 0%, #5be858 80%);
	background: -moz-linear-gradient(241deg, #00c2ff 0%, #5be858 80%);
}

.about-page-css p2 {
	/* Adds color and lots of contrast */
	filter: contrast(150%);
	-webkit-filter: contrast(150%);
	-moz-filter: contrast(150%);
	-o-filter: contrast(150%);
	-ms-filter: contrast(150%);
	cursor: pointer;
	margin: 16px;
	box-shadow: 3px 3px 10px 1px #000000;
	font-size: 23px;
	color: rgb(231, 231, 231);
	background: #9e31c4;
	background: linear-gradient(79deg, #9e31c4 0%, #64b5e3 80%);
	background: -webkit-linear-gradient(79deg, #9e31c4 0%, #64b5e3 80%);
	background: -moz-linear-gradient(79deg, #9e31c4 0%, #64b5e3 80%);
	margin: 16px;
}

.about_p_images {
	max-width: 100%;
	height: auto;
	width: auto;
	margin: 16px;
	transform: scale(0.5, 0.5);
	-ms-transform: scale(0.5, 0.5);
	-webkit-transform: scale(0.5, 0.5);
}

.video-player {
	width: 100%;
	height: auto;
}

/*  about page for mobile */
@media screen and (max-width: 960px) {
	.about-page-css h1 {
		background-size: 1px;
		background-image: url(https://media.giphy.com/media/xUPGcfG7LQs4a7rIsM/giphy-downsized.gif);
		margin-top: 55px;
		font-size: 25px;
		color: rgb(121, 73, 168);
	}
	.video-rapper {
		/*? Need to fix later */
	}
	.video-player {
		width: 100% !important;
		height: auto !important;
	}
}

